.filmsList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
  margin-bottom: 30px;
  min-height: 600px;
}

.films {
  position: relative;
  width: 280px;
  height: 100%;
  border-radius: 5px;
  background-color: white;
  transition: box-shadow 500ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
}

.films:hover,
.films:focus {
  box-shadow: 8px 12px 14px 2px rgba(71, 71, 71, 0.6);
}

.filmCover {
  position: relative;
  width: 100%;
  height: 398px;
  border-radius: 5px;
}

.filmCard {
  height: 100%;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.filmTitle {
  font-size: 16px;
  font-weight: 700;
  line-height: 1.33;
  color: #000000;
}

.filmRating {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 36px;
  height: 16px;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
  background-color: #8ddfff;
  color: white;
}

@media screen and (min-width: 768px) {
  .filmsList {
    gap: 32px;
    margin-top: 60px;
    margin-bottom: 60px;
  }

  .films {
    width: 336px;
  }

  .filmCover {
    height: 478px;
  }

  .filmTitle {
    font-size: 18px;
  }
}

@media screen and (min-width: 1280px) {
  .filmsList {
    gap: 32px;
  }

  .films {
    width: 280px;
    height: 100%;
  }

  .filmCover {
    height: 398px;
  }

  .filmTitle {
    font-size: 16px;
  }

  .filmRating {
    width: 40px;
    height: 20px;
  }
}
